<template>
    <div id="Home">
        <div v-for="(item,index) in menus">
            <li class="textalign-left width100percent FontSize-22px">{{item.name}}</li>

            <div class="width100percent" style="height:160px;">
                <div v-for="(subitem,subindex) in item.children" class="display-flex float-left"
                     style="flex-direction: row;width:200px;height:160px;">
                    <div class="width100percent height100percent display-flex"
                         style="flex-direction: column;align-items:center;"
                         @click="onclickmenuitem(subitem)">
                        <div :style="menuimg(subitem)"></div>
                        <div class="width100percent FontSize-16px textalign-center margin-top6px">{{subitem.name}}</div>
                    </div>

                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import menuconfig from '@/common/buss/menusconfig'
    import router from "@/router";
    export default {
        name: "Home",
        data() {
            return {
                menus: menuconfig.menustree


            };
        },

        computed: {
            menuimg() {
                return function (item) {
                    let result = "width:180px;height:120px;;";
                    return item.screenshot ? result + "background:url('" + item.screenshot + "');background-size:100% 100%;" : result + "background-color:#666;";
                }
            }
        },
        mounted() {
        },

        methods: {
            onclickmenuitem(item) {
                if(item.router){
                    router.push(item.router);
                }
                else if(item.url)
                {
                    window.open(item.url)
                }
            }

        },
        watch: {}
    }
</script>

<style scoped>
    #Home {
        padding: 12px;
        box-sizing: border-box;
        display: flex;
        flex-flow: column;
        overflow-y: auto;
        height: 100%;
    }
</style>
